import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import TablePagination from "@mui/material/TablePagination";

const dispatchedEvents = [];

const SrsMuiTablePagination = function (props, ref) {
  return (
    <TablePagination {...props} theme={props.muiTheme} component="div">
      <slot />
    </TablePagination>
  );
};

const styles = `
  /* Custom styles for the MuiTablePagination component */
`;

const props = [
  {
    name: "count",
    defaultValue: 0,
    type: "number",
  },
  {
    name: "page",
    defaultValue: 0,
    type: "number",
  },
  {
    name: "rowsPerPage",
    defaultValue: 10,
    type: "number",
  },
  {
    name: "rowsPerPageOptions",
    defaultValue: [10, 25, 50],
    type: "object",
  },
  {
    name: "onChangePage",
    defaultValue: null,
    type: "function",
  },
  {
    name: "onChangeRowsPerPage",
    defaultValue: null,
    type: "function",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiTablePagination),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
